import React, { Component } from 'react'
// import {useQuery}  from 'react-apollo'
import {useQuery}  from 'next-with-apollo'
import gql from 'graphql-tag'

const ALL_QUERY_ITEMS = gql`
  query ALL_QUERY_ITEMS{
    items{
      id
      title
      price
      description
      image
      largeImage
    }
  }
`

class Items extends Component {
  render() {
    return (
      <div>
        <p>these are items</p>
        <useQuery query={ALL_QUERY_ITEMS}>
          {payload => {console.log(payload)
            return <p>these are the child items</p>
          }}
        </useQuery> 
      </div>
    )
  }
}

export default Items
